.sidebar {
  flex: 3;
  margin: 10px 20px 20px 20px;
  background-color: #fdfdfd;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.sidebarItem {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.sidebarItem > img {
  width: 380px;
  height: 300px;
  margin-top: 15px;
}
.sidebarItem > p {
  margin-top: 5px;
  margin-bottom: 15px;
  padding: 15px;
}

.sidebarTitle {
  margin: 10px;
  padding: 5px;
  border-top: 1px solid #a7a4a4;
  border-bottom: 1px solid #a7a4a4;
  width: 80%;
  font-family: 'Varela Round', sans-serif;
  font-size: 14px;
  font-weight: 600;
  color: #222;
  line-height: 25px;
  text-align: center;
}

.sidebarList {
  list-style: none;
  margin-bottom: 30px;
}
.sidebarListItem {
  display: inline-block;
  width: 50%;
  margin-top: 15px;
  cursor: pointer;
}

.sidebarSocial {
  width: 250px;
  margin-top: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.sidebarIcon {
  font-size: 24px;
  margin-left: 10px;
  cursor: pointer;
}